	module.exports = {"data":{"markdownRemark":{"html":"<p>When I plugged in an USB stick in the USB3 front panel of my workstation dop, there was no response. The USB stick's light didn't turn on.</p>\n<p>After I opened up the sidepanel the USB plug to the motherboard wasn't plugged in, so I fixed that.</p>\n<p>After rebooting, I was greeted with this ugly message:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">Error: no such partition found \ngrub rescue&gt;</code></pre>\n      </div>\n<p>I tried using grub rescue. But using <code class=\"language-text\">ls</code> I can't find the boot partion. Then I notice, the bootdisk isn't showing up in the EFI.</p>\n<p>After opening up the sidepanel again I notice the power is not plugged in to my SSD, the bootdisk of the machine... Strangely, after plugging it in, the same error occurs.</p>\n<p>Booting from an Ubuntu live CD, all drives are visible, but the main SSD (/dev/sda2) shows an error in gparted. It does have the bios_grub flag. It gives the error:</p>\n<p><code class=\"language-text\">unable to detect file system</code></p>\n<p>After deleting the /dev/sda4 bios_grub partition of 17MB this leaves an EFI fat32 partition with boot flag (dev/sda3) and main partition /dev/sda1 with /boot.</p>\n<p>Reboot: doesn't change anything.</p>\n<p>Now I'm really need to use the system, so I try rebooting with super grub disk. The system boots immediately with first found settings. I think I'll leave it like this because I want to upgrade the SSD in the near future anyway.</p>","htmlAst":{"type":"root","children":[{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"When I plugged in an USB stick in the USB3 front panel of my workstation dop, there was no response. The USB stick's light didn't turn on."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"After I opened up the sidepanel the USB plug to the motherboard wasn't plugged in, so I fixed that."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"After rebooting, I was greeted with this ugly message:"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"div","properties":{"className":["gatsby-highlight"]},"children":[{"type":"text","value":"\n      "},{"type":"element","tagName":"pre","properties":{"className":["language-text"]},"children":[{"type":"element","tagName":"code","properties":{"className":["language-text"]},"children":[{"type":"text","value":"Error: no such partition found \ngrub rescue>"}]}]},{"type":"text","value":"\n      "}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"I tried using grub rescue. But using "},{"type":"element","tagName":"code","properties":{"className":["language-text"]},"children":[{"type":"text","value":"ls"}]},{"type":"text","value":" I can't find the boot partion. Then I notice, the bootdisk isn't showing up in the EFI."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"After opening up the sidepanel again I notice the power is not plugged in to my SSD, the bootdisk of the machine... Strangely, after plugging it in, the same error occurs."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Booting from an Ubuntu live CD, all drives are visible, but the main SSD (/dev/sda2) shows an error in gparted. It does have the bios_grub flag. It gives the error:"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"element","tagName":"code","properties":{"className":["language-text"]},"children":[{"type":"text","value":"unable to detect file system"}]}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"After deleting the /dev/sda4 bios_grub partition of 17MB this leaves an EFI fat32 partition with boot flag (dev/sda3) and main partition /dev/sda1 with /boot."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Reboot: doesn't change anything."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Now I'm really need to use the system, so I try rebooting with super grub disk. The system boots immediately with first found settings. I think I'll leave it like this because I want to upgrade the SSD in the near future anyway."}]}],"data":{"quirksMode":false}},"timeToRead":1,"excerpt":"When I plugged in an USB stick in the USB3 front panel of my workstation dop, there was no response. The USB stick's light didn't turn onâ€¦","frontmatter":{"title":"Dop won't boot and USB issues","cover":"/cover/","hash":"589c52c3bab0250648a2f8f20d8f6d6e44b9af95","date":"02-03-2015","category":"mypc","tags":["mypc"]},"fields":{"nextTitle":"Dop SSD update","nextSlug":"/dop-ssd-update","prevTitle":"Utopic Dop","prevSlug":"/utopic-dop","slug":"/dop-wont-boot-and-usb-issues"}}},"pathContext":{"slug":"/dop-wont-boot-and-usb-issues"}}

